import React from "react";
import Layout from "components/layout";
import SocialMediaSharingButtons from "components/blog/SocialMediaSharingButtons.jsx";
import BlogAuthor from "components/blog/BlogAuthor.jsx";
import heroImg from "assets/img/hero-bg-img.jpg";
import { path } from "ramda";
import UndecoratedInternalLink from "components/reusable/atoms/links/UndecoratedInternalLink";
import { formatKebabCase } from "utils/formatting";

import { createBlogSchema } from "utils/seoSchema";
import JsonSchema from "components/JsonSchema";
import { createBody } from "utils/contentful";
import Footer from "./footer";

// The follow component returns the layout of a single blog page - regardless of the data model
// behind the blog post
// The most important part of this is understanding that a post's data model is nested, which means
// different micro-components (paragraphs, headers, images, etc.) can reference each other almost infinitely.
// These micro-components are called nodeTypes. Each nodeType has the following structure (with some of the keys
// being null sometimes): {nodeType, content: [nodeType object, nodeType object], marks: [{value}, {value}], data: {uri}, value}
// nodeType is a string that can take on the values: blockquote, text, paragraph, header-[1-6], list-item, embedded-asset-image, hyperlink, etc.
// marks refers to the styling of text, with the value potentially having the values of "italic", "bold," "underlined," etc.
// As shown, marks is an arroy of jsons that each have a value key, which represents that a certain piece of text can have multiple stylings
// data is a json. for hyperlinks, the uri key is there which is the URL the hyperlink is supposed to point to. The data key can be null.
// value is a string representing the text one is supposed to show in either a paragraph or text node. It can be null. It can also be an empty string.
// content is an array of nodeType objects. This is where the nestedness comes from as one nodeType can reference many others.
// We assume a json object is passed in with the following fields:
// title: string representing the title of the post
// date: string representing the date of the post
// author: json object. Can be undefined. If not undefined, assumed to have {fields: {name, biography, picture: {file: {url}}} structure
// body: json object. This is an arbitrary collection of nodeTypes.
// videoLink: string representing the url of an embedded video to include. Can be undefined and is shown between body and bodyBottom.
// bodyBottom: same as body except can be undefined
// brexExperience: same as body except can be undefined. This is what's shown in the Brex Experience area
// IMPORTANT
// There is a lot of functionality in the Contentful UI that is not supported here. For example, it's technically possible for a blog to compose itself with
// another blog post. In the event that someone does something in the blog UI that's not supported here, it will either be ignored and not shown or the formatting
// of the blog will mess up (or the website will crash when trying to be deployed).
// Because of this, it's very important that all changes made here are surfaced (read over-communicated) to the people writing the blog posts.
//
// It's also important to remember that to see the most up to date content, the website must be built again (whether locally or in production)

// /
// Takes the brexExperience field and renders the subsection
// If it's null or undefined, we don't return anything
// /
const brexExperienceCreation = (brexExperience) => {
  if (brexExperience === null || brexExperience === undefined) {
    return undefined;
  }
  return (
    <div>
      <div className="post-blog-stripe" />
      <h2>Brex Experience</h2>
      {createBody(brexExperience)}
    </div>
  );
};

const BlogPost = ({ pageContext: { contentfulData }, location, indexPage }) => {
  const postThumbnail = contentfulData.heroImage
    ? contentfulData.heroImage.fields.file.url
    : heroImg;

  return (
    <Layout
      whiteContentBackground
      location={location}
      ogTitle={`Brex Log | ${contentfulData.title}`}
      ogImage={postThumbnail}
      ogDescription={
        contentfulData.metaDescriptionTag ||
        "Guides and data insights for startup decision makers."
      }
      title={contentfulData.metaTitleTag}
      description={contentfulData.metaDescriptionTag}
      indexPage={indexPage}
    >
      <SocialMediaSharingButtons location={location} orientation="vertical" />
      <div className="post">
        <h1 className="post-title">{contentfulData.title}</h1>
        {contentfulData.author && (
          <UndecoratedInternalLink
            to={`/blog/author/${formatKebabCase(
              contentfulData.author.fields.name,
            )}`}
          >
            <div className="post-author-top">
              WRITTEN BY {contentfulData.author.fields.name}
            </div>
          </UndecoratedInternalLink>
        )}
        <div className="post-date">{contentfulData.date}</div>
        <img src={postThumbnail} alt={contentfulData.heroImgAlt} />
        <div className="post-text">
          {createBody(contentfulData.body)}
          {contentfulData.videoLink && (
            <iframe src={contentfulData.videoLink} title="Video" />
          )}
          {contentfulData.bodyBottom && createBody(contentfulData.bodyBottom)}
          {brexExperienceCreation(contentfulData.brexExperience)}
        </div>
        {contentfulData.author && (
          <BlogAuthor authorData={contentfulData.author} />
        )}
      </div>
      <Footer articleLinks={contentfulData.articleLinks} />
      <JsonSchema
        data={createBlogSchema(
          `https://brex.com/blog/${contentfulData.urlPath}`,
          contentfulData.title,
          postThumbnail.slice(2),
          path(["author", "fields", "name"], contentfulData) ||
            "Matthew Lynley",
          contentfulData.date,
        )}
      />
    </Layout>
  );
};

export default BlogPost;
