import React from "react";
import {
  LinkedinShareButton,
  FacebookShareButton,
  TwitterShareButton,
} from "react-share";
import { withClickAnalytics } from "hocs/analytics";
import { SocialIcon } from "react-social-icons";

const LinkedinButton = withClickAnalytics(
  "Social Share",
  "blog.linkedin",
)(LinkedinShareButton);
const FacebookButton = withClickAnalytics(
  "Social Share",
  "blog.facebook",
)(FacebookShareButton);
const TwitterButton = withClickAnalytics(
  "Social Share",
  "blog.twitter",
)(TwitterShareButton);

const SocialMediaSharingButtons = ({ location, orientation = "vertical" }) => (
  <ul className={`share-${orientation}`}>
    <li>
      <LinkedinButton
        url={location.href}
        windowWidth={750}
        windowHeight={600}
        className="button"
      >
        <SocialIcon
          network="linkedin"
          color="black"
          style={{ height: 40, width: 40 }}
        />
      </LinkedinButton>
    </li>
    <li>
      <FacebookButton
        url={location.href}
        windowWidth={750}
        windowHeight={600}
        className="button"
      >
        <SocialIcon
          network="facebook"
          color="black"
          style={{ height: 40, width: 40 }}
        />
      </FacebookButton>
    </li>
    <li>
      <TwitterButton
        url={location.href}
        windowWidth={750}
        windowHeight={600}
        className="button"
      >
        <SocialIcon
          network="twitter"
          color="black"
          style={{ height: 40, width: 40 }}
        />
      </TwitterButton>
    </li>
  </ul>
);

export default SocialMediaSharingButtons;
