import React from "react";

import UndecoratedInternalLink from "components/reusable/atoms/links/UndecoratedInternalLink";
import { formatKebabCase } from "utils/formatting";

const BlogAuthor = ({ authorData }) => (
  <div>
    <div className="post-blog-stripe" />
    <div className="post-author">
      {!!authorData.fields.picture && (
        <img
          className="photo"
          src={authorData.fields.picture.fields.file.url}
          alt="Author"
        />
      )}
      <div className="info">
        <UndecoratedInternalLink
          to={`/blog/author/${formatKebabCase(authorData.fields.name)}/`}
        >
          <div className="name">{authorData.fields.name}</div>
        </UndecoratedInternalLink>
        <div className="description">{authorData.fields.biography}</div>
      </div>
    </div>
    <div className="post-blog-stripe" />
  </div>
);

export default BlogAuthor;
