import React from "react";
import arrow from "assets/img/icons/orange/link-arrow.svg";

import { FooterProps } from "components/learningCenter/types";
import {
  Wrapper,
  Title,
  FooterContainer,
  Image,
  FooterItemContainer,
  LinkArrow,
  ImageTitle,
} from "./elements";

const Footer: React.SFC<FooterProps> = ({ articleLinks }) => (
  <Wrapper>
    <Title>Related Articles</Title>
    {articleLinks && (
      <FooterContainer>
        {articleLinks.map(
          (item) =>
            item.fields && (
              <FooterItemContainer
                to={`blog/${item.fields.urlPath}`}
                key={item.fields.urlPath}
              >
                <LinkArrow>
                  <img src={arrow} alt="arrow" />
                </LinkArrow>
                {item.fields.thumbnailImage &&
                  item.fields.thumbnailImage.fields.file && (
                    <Image src={item.fields.thumbnailImage.fields.file.url} />
                  )}
                <ImageTitle>{item.fields.title}</ImageTitle>
              </FooterItemContainer>
            ),
        )}
      </FooterContainer>
    )}
  </Wrapper>
);

export default Footer;
